import React, { useState, useEffect } from 'react';
import products from '../data-files/products';
import ProductsList from '../components/ProductsList';
import CommentsList from '../components/CommentsList';
import AddCommentForm from '../components/AddCommentForm';
import VotingSection from '../components/VotingSection';
import PageError404 from './ErrorPage404';

const ProductPage = ({match}) => {
    const productId = match.params.id;
    const product = products.find(product => product.id === productId);
    
    const [productInfo, setProductInfo] = useState({ thumbsUp: 0, thumbsDown: 0, userVote: '', comments: []});
    
    useEffect(() => {
        const fetchData = async() => {
            const result = await fetch(`/api/products/${productId}`);
            const body = await result.json();
            setProductInfo(body);
        }
        fetchData();
        }, [productId]);
    
    if (!product) return <PageError404 />
    const company = product.company;

    const otherProducts = products.filter((product) => { return product.id !== productId && product.company === company});
        
    return (
        <>
            <h1>{product.title}</h1>
            <h4>Manufacturer: {company}</h4>
            <VotingSection 
                 productId={productId}
                 thumbsUp={productInfo.thumbsUp}
                 thumbsDown={productInfo.thumbsDown}
                 setProductInfo={setProductInfo}
             />

            <h3>Review:</h3>
            {
                product.content.map((content, key) => (
                    <p key={key} >{content}</p>)
                )

            }
            <AddCommentForm productId={productId} setProductInfo={setProductInfo} />
            <CommentsList comments={productInfo.comments} />

            <h3>{`Other "${company}" Products:`}</h3> 
            <ProductsList products={otherProducts}/>


        </>
    )
}

export default ProductPage;