import React from 'react';
import { IconContext } from "react-icons";
import { FiThumbsUp , FiThumbsDown } from 'react-icons/fi';

const VotingSection = ({ productId, thumbsUp, thumbsDown, setProductInfo }) => {
    const postUserVote = async (vote) => {
        const result = await fetch (`/api/products/${productId}/vote`, {
            method: 'post',
            body: JSON.stringify({vote: vote}),
            headers: {
                'content-type' : 'application/json'
            }
        });
        const body = await result.json();
        setProductInfo(body)
    }

    return(
        <div id="votes-section">         
            <p>
                Product Recommendation: 
                <IconContext.Provider value={{ size: "1.5em" }}>
                    <FiThumbsUp onClick={() => {postUserVote('up') }} /> 
                    <span className="vote-count">{thumbsUp} </span>
                    
                    <FiThumbsDown onClick={() => {postUserVote('down') }} /> 
                    <span className="vote-count">{thumbsDown} </span>
                </IconContext.Provider>
            </p>

        </div>
    );
}

export default VotingSection;